import { JSX } from "react"
import { RPTooltip } from "@rp/react/web/common/base-components"
import { Skeleton, Typography } from "@rp/react/web/mui/core"
import { TrendingDown, TrendingUp } from "@rp/react/web/mui/icons"

type WatchlistPerformance = {
  color: string
  backgroundColor: string
  direction: string
  icon?: JSX.Element
}

const positive = {
  color: "green.500",
  backgroundColor: "green.A700",
  direction: "Up",
  icon: <TrendingUp sx={{ fontSize: 16 }} />,
}

const negative = {
  color: "red.500",
  backgroundColor: "red.A700",
  direction: "Down",
  icon: <TrendingDown sx={{ fontSize: 16 }} />,
}

type Props = {
  averageChangePercentage: number | null
  isLoading?: boolean
  hideBackground?: boolean
}

const WatchlistAverageChangePercentageChip = ({
  averageChangePercentage,
  isLoading = false,
  hideBackground = false,
}: Props) => {
  if (isLoading) {
    return <Skeleton width={70} height={20} sx={{ my: 0.5 }} />
  }
  if (averageChangePercentage === 0 || averageChangePercentage === null) {
    return null
  }

  const performance: WatchlistPerformance = averageChangePercentage >= 0 ? positive : negative

  return (
    <RPTooltip
      title="Average daily percentage change of the watchlist."
      placement="top"
      PopperProps={{ sx: { textAlign: "center" } }}
    >
      <Typography
        sx={{
          backgroundColor: hideBackground ? undefined : performance.backgroundColor,
          color: performance.color,
          alignItems: "center",
          borderRadius: "5px",
          display: "flex",
          fontSize: 12,
          fontWeight: 700,
          gap: 0.5,
          letterSpacing: "0.25px",
          lineHeight: "16px",
          padding: hideBackground ? "6px 0px" : "6px 10px",
        }}
      >
        <span>{performance.direction}</span>
        <span>{averageChangePercentage.toFixed(2)}%</span>
        {performance.icon}
      </Typography>
    </RPTooltip>
  )
}

export { WatchlistAverageChangePercentageChip }
